import { useSelector } from "react-redux";
import { useDispatch } from "react-redux"
import { crmApiNotes } from "../api/crmapi";
import  crmApiEmails  from "../api/crmapi";
import { emailHide, emailSendData, emailShow, noteHide, noteShow } from "../store";

export const useTools=()=>{

    const dispatch=useDispatch();
    const {noteOpen}=useSelector(state=>state.note)
    const {emailOpen,emailData}=useSelector(state=>state.email)
    
    const notesShow=()=>{
        dispatch(noteShow())
    }
    const emailsShow=()=>{
        dispatch(emailShow())
    }
    const notesHide=()=>{
        dispatch(noteHide())
    }
    const emailsHide=()=>{
        dispatch(emailHide())
    }

    const saveNote=async(state)=>{
        
        dispatch(noteHide())
        try {
            const { data } = await crmApiNotes.post('/', state );

        } catch (error) {
            
        }
    }
    const saveEmail=async(state)=>{
        // console.log(state);
        // dispatch(emailSendData())
        dispatch(emailHide())
        try {
            const { data } = await crmApiEmails.post('/', state );

        } catch (error) {
            
        }
    }
    return {
        noteOpen,
        emailOpen,
        emailData,
        saveNote,
        notesShow,
        saveEmail,
        emailsShow,
        emailsHide,
        notesHide
    }
}

