import React from 'react'
import { useDispatch } from 'react-redux'
import { useSelector } from 'react-redux'
import { cambioLayout, mostrarTodo } from '../store';

export const useLayout = () => {
    
    const dispatch=useDispatch();
    const {tipo,showAll}=useSelector(state=>state.layout)
    
    const changeLayout=(data='')=>{
        dispatch(cambioLayout(data))
    }
    const muestraTodo=(data)=>{
      dispatch(mostrarTodo(data))
  }

  return {
    showAll,
    tipo,
    changeLayout,
    muestraTodo
  }
}
