import { createSlice } from '@reduxjs/toolkit';

export const emailSlice = createSlice({
    name: 'email',
    initialState: {
        emailOpen: false,
        emailData:''
    },
    reducers: {
        emailShow: (state, /* action */ ) => {
         state.emailOpen= true;
        },
        emailHide: (state, /* action */ ) => {
         state.emailOpen= false;         
        },
        // emailSendData: (state,{payload})=>{
        //     state.emailData=payload
        // }
    }
});


// Action creators are generated for each case reducer function
export const { emailShow, emailHide} = emailSlice.actions;