import React,{useEffect,useState} from 'react';

import { crmApiNotes,crmApiEmails } from '../../../api/crmapi'
import { useTools } from '../../../hooks';



export const ContactCard = ({value=''}) => {
    
    const imagen=require('../../../image/avatar1.jpg');

    const [notes, setNotes] = useState([])
    const [emails, setEmails] = useState([])
    const {notesShow,emailsShow,notesHide,emailsHide,emailData}=useTools();

    let count=0;
    let counte=0;
    useEffect(() => {
      loadData()
      loadDataEmail()
      }, [emails,notes])

    const loadData=async()=>{
      const resp = await crmApiNotes.get( `/${value.email_c}`,value);
          setNotes(resp.data);
    } 
    const loadDataEmail=async()=>{
      const resp = await crmApiEmails.get( `/${value.email_c}`,value);
          setEmails(resp.data);
    } 
    const showNotes=()=>{
      emailsShow();
      notesHide();
     
    }
    const showEmails=()=>{
      notesShow();
      emailsHide()
    }
  return (
    // left left__select px-4
    <div className="border-top left">
        {/* Foto del contacto */}
        <div className='left__head'>
            <div className='left__marco'>
                <img src={imagen} className='left__image'/>
            </div>
        </div>
        {/* Nombre del contacto*/}
        <div className='left__name'>
            <span className='d-flex'><h3 className='left__name_main'>{value.name_c}</h3><h3>{value.lastname_c}</h3></span>
        </div>      
        {/* Titulo del contacto */}
        <div >
        <span className='left__titulo'><h5 >{value.cargo_c}</h5></span>
        </div>
        {/* Informacion del contacto */}
        <div>
            <label htmlFor="">Email</label>
            <div className='left__line'></div>
          <span className='left__info'><p>{value.email_c}</p></span> 

          <label htmlFor="">Phone</label>
          <div className='left__line'></div> 
          <span className='left__info'><p>{value.phone_c}</p></span>  

          <label htmlFor="">Country</label>
          <div className='left__line'></div>
          <span className='left__info'><p>{value.country_c}</p></span> 
          
          <label htmlFor="">Company</label>
          <div className='left__line'></div>
          <span className='left__info'><p>{value.company_c}</p></span>  
        </div>

        

        {/* Notas */}
        <div className='right__info right__info--iz' onClick={showNotes}>
           <label htmlFor="">Last Note</label>
          <div className='left__line'></div>
          <div >
            {
              notes.map((note)=>{
                count++;
                //Permitir solo una nota
                return(
                  <div className='left__info' key={note._id}>
                    {
                      count===1 &&<p >
                      {note.note}
                      </p>
                    }
                  </div>
                      
                  
                )
              })
            }
            
          </div> 
        </div>
        {/* Emails */}
        <div className='right__info right__info--iz' onClick={showEmails}>
          <label htmlFor="">Last Email</label>
          <div className='left__line'></div>
          <div >
            {
             emails.map((email)=>{
                counte++;
                //Permitir solo una nota
                return(
                  <div className='left__info' key={email._id}>
                    {
                      counte===emails.length &&<p >
                      {email.data}
                      </p>
                    }
                  </div>
                      
                  
                )
              })
            }
            
          
          </div>
        </div>
    </div>
  )
}
