import { createSlice } from '@reduxjs/toolkit';

export const tableContactSlice = createSlice({
    name: 'tableContact',
    initialState: {
        cell: false,
        isdeleteCont:false,
        showCont:false,
        search:false,
        searchChange:{
            data:"",
            type:""
        }
    },
    reducers: {
        
        clickOn:(state)=>{
            state.cell=true
        },
        clickOff:(state)=>{
            state.cell=false
        },
        deletingCellContact:(state,{payload})=>{
            state.isdeleteCont=payload
        },
        deleteCellContact:(state,{payload})=>{
            state.isdeleteCont=payload
        },
        activeSearch:(state,{payload})=>{
            state.search=payload
        },
        deactiveSearch:(state)=>{
            state.search=false
        },
        changeSearch:(state,{payload})=>{
            state.searchChange=payload
        }
        // showContact:(state)=>{
        //     if(state.isdeleteCont){
        //         state.showCont=false
        //     }else{
        //         state.showCont=true
        //     }
            
        // },
        // hideContact:(state)=>{
        //     state.showCont=false
        // }
    }
});


// Action creators are generated for each case reducer function
export const { clickOn,clickOff,deletingCellContact,deleteCellContact,activeSearch,changeSearch,deactiveSearch} = tableContactSlice.actions;
// export const { clickOn,clickOff,deletingCellContact,deleteCellContact,showContact,hideContact} = tableContactSlice.actions;