import React,{useState,useEffect} from 'react'
import { crmApiCont } from '../../../api/crmapi';
import { useCard, useForm, useLayout } from '../../../hooks';


export const SearchContact = () => {

    const [contact,setContact]=useState ([]);
    const [state, setState] = useState({})
    const [change, setChange] = useState(true)
    const {seleccion,onInputChange}=useForm('')
    const {searchAnilize,searchActive}=useCard();
    const {muestraTodo}=useLayout()
    let choice='';
    useEffect(() => {
        loadData()
       
        }, [])
    const loadData=async()=>{
        const resp = await crmApiCont.get( '/');
        setContact(resp.data);
    } 
    // var matches = document.querySelectorAll('.chngRadio');

   const handleRadio=({target})=>{
        const { name, value } = target;
        setState({
            ...state,
            [ name ]: value
        });
       
   }
   const submit=(e)=>{
    e.preventDefault();
    setChange(!change)
    searchActive(change)
    muestraTodo(false)
    searchAnilize({seleccion,type:state.search})
    // console.log(seleccion)
   }
  return (
    <div>
        <div className="border-top left left__select px-4">
            <h4 className="text-center">Contacts</h4>
            <div className="left__select">
                <input type="radio" className="left__radio" name="search" id="fecha" onChange={handleRadio} value="Date" />Date <br/>
                <input type="radio" className="left__radio" name="search" id="nombreC" onChange={handleRadio} value="Name"/>Name <br/>
                <input type="radio" className="left__radio" name="search" id="apellido" onChange={handleRadio} value="Last_name"/>Last_name<br/>
                <input type="radio" className="left__radio" name="search" id="apellido" onChange={handleRadio} value="Cargo"/>Cargo<br/>
                <input type="radio" className="left__radio" name="search" id="dominio" onChange={handleRadio} value="Email"/>Email<br/>
                <input type="radio" className="left__radio" name="search" id="pais" onChange={handleRadio} value="Country"/>Country<br/>
                <input type="radio" className="left__radio" name="search" id="company" onChange={handleRadio} value="Company"/>Company<br/>
                <input type="text" className="form-control mt-3 left__search" placeholder="Buscar por"/><br/>
                
                 
            </div>
            <form onSubmit={submit}>
                <select name="seleccion" id="seleccion" onChange={ onInputChange } defaultValue={seleccion}>
                <option value="">Seleccione uno</option>
                        {
                
                            contact.map(item=>{
                                // console.log(state);
                                return(
                                    <>
                                        
                                        <option  name="seleccion"
                                         key={item._id}>
                                            {
                                            state.search==="Date"?item.fecha_creacion:
                                            state.search==="Name"?item.name_c:
                                            state.search==="Last_name"?item.lastname_c:
                                            state.search==="Cargo"?item.cargo_c:
                                            state.search==="Country"?item.country_c:
                                            state.search==="Email"?item.email_c:
                                            state.search==="Company"?item.company_c:null
                                            }
                                        </option>
                                    </>
                                )
                            })
                        }
                
                    </select>
                <div className="left__button">
                    <button type="submit" >Search</button>
                </div>
            </form>

        </div>
    </div>
  )
}
