import { createSlice } from '@reduxjs/toolkit';

export const infoContactSlice = createSlice({
    name: 'infoContact',
    initialState: {
        name_c: '',
        lastname_c:'',
        cargo_c:'',
        email_c:'',
        phone_c:'',
        country_c:'',
        company_c:'',
        domain:''
        
    },
    reducers: {
        cardContact: (state, {payload} ) => {
         state.name_c=payload.name_c;
         state.lastname_c=payload.lastname_c;
         state.cargo_c=payload.cargo_c;
         state.email_c=payload.email_c;
         state.phone_c=payload.phone_c;
         state.country_c=payload.country_c;
         state.company_c=payload.company_c;
         state.company_c=payload.domain;
       
        },
        cardContactClean:(state) => {
            state.name_c='';
            state.lastname_c='';
            state.cargo_c='';
            state.email_c='';
            state.phone_c='';
            state.country_c='';
            state.company_c='';
            state.domain='';
          
           }
    }
});


// Action creators are generated for each case reducer function
      
export const { cardContact, cardContactClean} = infoContactSlice.actions;