import Tabla_contactos from "../Tablas/tabla_contactos";
import Tabla_company from "../Tablas/tabla_company";

// import state_company from "../../redux/state_company"

// import Set from "../set";
import { SetContact } from '../SetOptions/setContact';
import { SetCompany } from '../SetOptions/setCompany';
import { CalendarPage } from "../../screens/calendar/pages/CalendarPage";
import { InfoContact } from "../Derecho/infoContact";
import { InfoCompany } from "../Derecho/infoCompany";
import { SetAddContact } from "../SetOptions/setAddContact";
import { SetAddCompany } from "../SetOptions/setAddCompany";
import { useCard, useLayout } from "../../hooks";
// type Information={
//   information:string;
// }

const LateralDerecho=({information})=> {
  const {values,valuesComp}=useCard();
  const {showAll}=useLayout();
    
   const selection=()=>{
     switch (information) {
      case "contacto":
        return (
          <>          
          <SetAddContact  />
          <Tabla_contactos />
          </>
        )
      case "company":
        return (
          <>
          <SetAddCompany  />
          <Tabla_company />
          </>
        )
      case "calendar": 
          return (
            <>
            <CalendarPage  />
            </>
          )
      case "info_contact": 
          return (
            <>
            <SetContact  />
            <InfoContact  />
            </>
          )
      case "info_company": 
          return (
            <>
            <SetCompany  />
            <InfoCompany value={valuesComp} />
            </>
          )
      default:
        break;
    }
   
   } 
    return(
        <>
          
         {selection()}
           
        </>

      )
}
export default LateralDerecho;