import React, { useState } from 'react'
import { useAuthStore, useCard, useForm, useLayout, useTools } from '../../hooks';

const infoNotes={
    note:'',

}
const infoEmails={
    emails:'',

}
export const SetContact = () => {

    const {note, onInputChange} = useForm(infoNotes);
    const {emails, onInputChange : inpChange} = useForm(infoEmails);
    const {saveNote,notesShow,saveEmail,emailsHide,notesHide,emailsShow}=useTools();
    const {user,startLogout}=useAuthStore();
    const {email}=user
    const {changeLayout}=useLayout();
    const {values}=useCard();
    const {email_c}=values;

    var date =new Date();
    var dd =String(date.getDate()).padStart(2,'0');
    var mm =String(date.getMonth()+1).padStart(2,'0');
    var yyyy = date.getFullYear();
    date = yyyy +'/'+mm +'/'+ dd ; 
    
   
    const chagesLayout=(data)=>{
        changeLayout(data)
    }
    const notas=()=>{
        notesShow();
        const nota=document.getElementById("setTextarea");
        nota.style.display="block"
       
    }
    const emailHandle=()=>{
        emailsShow();
        const nota=document.getElementById("setEmail");
        nota.style.display="block"
    }
    const logout=()=>{
        startLogout();
    }
    const cancel=()=>{
        emailsHide()
       
    }
    const cancelNote=()=>{
        notesHide()
      
    }
    const handleForm=(e)=>{
        e.preventDefault();
        
        saveNote({note,email_c,date})
        const correo=document.getElementById("setTextarea");
        correo.style.display="none"
    }
    const handleFormEmail=(e)=>{
        e.preventDefault();
        saveEmail({data:emails,email_c,date,email})
        emailsHide();
        const nota=document.getElementById("setEmail");
        nota.style.display="none"
    }
   
  return (
    <>
        <div className="row mb-3">
            <div className="row">
                <div className="col col-6">
                    <nav className="navbar navbar-expand-lg navbar-light bg-light me-0">
                        <div className="container-fluid">
                            <div className="collapse navbar-collapse justify-content-start" id="navbarNavAltMarkup">
                                <div className="navbar-nav">
                                    <a className="nav-link active" aria-current="page" href="#" onClick={notas}>Notes</a>
                                    <a className="nav-link" href="#" onClick={emailHandle}>Emails</a>
                                    <a className="nav-link" href="#" onClick={()=>chagesLayout('calendar')}>Meetings</a>
                                </div>
                            </div>
                            {/* Nombre de usuario conectado */}
                
                        </div>
                    </nav>
                </div>
                <div className="col col-6 ">
                    <nav className="navbar navbar-expand-lg navbar-light bg-light me-0">
                        <div className="container-fluid">
                            <div className="collapse navbar-collapse justify-content-end" id="navbarNavAltMarkup">
                
                                <div >
                                    <button type="button" onClick={logout} className="btn btn-outline-primary ">Logout</button>
                                </div>
                            </div>
                            {/* Nombre de usuario conectado */}
                
                        </div>
                    </nav>
                    <div>
                        <p className='float-end me-4'>{user.name}</p>
                    </div>
                </div>
            </div >
            {/* Aqui se escriben las notas y emails */}
            <div className="row entradas__set_textarea" id='setTextarea'>
                <form onSubmit={handleForm}>
                    <div><textarea name="note" id="" cols="30" rows="7" onChange={onInputChange} defaultValue={note}/></div>
                    <div className="d-flex ">
                        <div>
                            <button type="submit" className="btn btn-outline-primary " >Save</button>
                        </div>
                        <div>
                            <button  className="btn btn-outline-primary mx-3" onClick={cancelNote}>Cancel</button>
                        </div>
                    
                    </div>
                </form>
            </div>
            <div className="row entradas__set_textarea" id='setEmail'>
                <form onSubmit={handleFormEmail}>
                    <div className='row'>
                        <div className='col col-1 pt-2'>
                            <p>From</p>
                            <p>Cc</p>
                            <p htmlFor="">Subject</p>
                        </div>
                        <div className='col col-11'>
                            <div className='my-2'><input type="email" name="from" value={email} className='entradas__subjet' pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$" /></div>
                            <div className='my-2'><input type="email" name="cc" className='entradas__subjet' pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"/></div>
                            <div className='my-2'><input type="email"name="para" value={email_c} className='entradas__subjet' pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"/></div>
                        </div>
                    </div>
                    
                    <div><textarea name="emails" id="emailText" cols="30" rows="7" onChange={inpChange} defaultValue={emails}/></div>
                    <div className="d-flex ">
                        <div>
                            <button type="submit" className="btn btn-outline-primary " >Save</button>
                        </div>
                        <div>
                            <button className="btn btn-outline-primary mx-3" onClick={cancel}>Cancel</button>
                        </div>
                    
                    </div>
                </form>
            </div>
        </div>
    </>
  )
}
