import React,{useState,useEffect} from 'react'
import { crmApiComp } from '../../../api/crmapi';
import { useCard, useForm, useLayout } from '../../../hooks';


export const SearchCompany = () => {


    const [company,setCompany]=useState ([]);
    const [state, setState] = useState({})
    const [change, setChange] = useState(true)
    const {seleccion,onInputChange}=useForm('')
    const {searchAnilize,searchActive}=useCard();
    const {muestraTodo}=useLayout()
    
    useEffect(() => {
        loadData()
       
        }, [])
    const loadData=async()=>{
        const resp = await crmApiComp.get( '/');
        setCompany(resp.data);
    } 
    const handleRadio=({target})=>{
        const { name, value } = target;
        setState({
            ...state,
            [ name ]: value
        });
       
   }
   const submit=(e)=>{
    e.preventDefault();
    setChange(!change)
    searchActive(change)
    muestraTodo(false)
    searchAnilize({seleccion,type:state.searchC})
    // console.log(seleccion)
   }
  return (
    <div>
        <div className="border-top left left__select px-4">
            <h4 className="text-center">Companies</h4>
            <div className="left__select">
                <input type="radio" className="left__radio" name="searchC" id="date" onChange={handleRadio} value="Date"/>Date <br/>
                <input type="radio" className="left__radio" name="searchC" id="empresa" onChange={handleRadio} value="Company"/>Company <br/>
                <input type="radio" className="left__radio" name="searchC" id="codigo" onChange={handleRadio} value="codigoPostal"/>Codigo Postal<br/>
                <input type="radio" className="left__radio" name="searchC" id="dominio" onChange={handleRadio} value="Domain"/>Domain<br/>
                <input type="radio" className="left__radio" name="searchC" id="pais" onChange={handleRadio} value="Country"/>Country<br/>
                <input type="radio" className="left__radio" name="searchC" id="language" onChange={handleRadio} value="Language"/>Language<br/>
                <input type="radio" className="left__radio" name="searchC" id="corrency" onChange={handleRadio} value="Currency"/>Currency<br/>
                <input type="text" className="form-control mt-3 left__search" placeholder="Buscar por"/><br/>
                
            </div>
            <form onSubmit={submit}>
                <select name="seleccion"  onChange={ onInputChange } defaultValue={seleccion}>
                <option value="">Seleccione uno</option>
                        {
                
                        company.map(item=>{
                                // console.log(state);
                                return(
                                    <option  name="seleccion"  key={item._id}>
                                        {
                
                                        state.searchC==="Date"?item.fecha_creacion:
                                        state.searchC==="Company"?item.nombreEmpresa:
                                        state.searchC==="codigoPostal"?item.codigoPostal:
                                        state.searchC==="Domain"?item.domain:
                                        state.searchC==="Country"?item.pais:
                                        state.searchC==="Language"?item.idioma:
                                        state.searchC==="Currency"?item.moneda:null
                                        }
                                    </option>
                                )
                            })
                        }
                
                    </select>
                <div className="left__button">
                    <button type="submit" >Search</button>
                </div>
            </form>

        </div>
    </div>
  )
}
