import { createSlice } from '@reduxjs/toolkit';

export const formCompanySlice = createSlice({
    name: 'triggerComp',
   
    initialState: {
        isOpenComp: false,
        isCloseCont:true, 
        tablaShowComp:true,
        sendingComp:false,
        sentComp:false,
        update:false,
        errorMessage: undefined,
    },
    reducers: {
     
        openFormCompany: ( state ) => {
            state.isOpenComp = true;      
            state.isCloseCont=false;     
            state.tablaShowComp=false; 
            state.errorMessage = undefined;
        },
        closeFormCompany: ( state, { payload } ) => {
            state.isOpenComp = false;   
            state.isCloseCont=true;           
            state.tablaShowComp=true; 
            state.errorMessage = undefined;
        },
        updateFormCompany:(state)=>{
            state.update=true;
        },
        clearErrorComp: ( state ) => {
            state.errorMessage = undefined;
        },
        sendingFormComany: ( state ) => {
            state.errorMessage = undefined;
        },
        sentFormComany: ( state ) => {
            state.errorMessage = undefined;
        }
    }
});


// Action creators are generated for each case reducer function
export const { openFormCompany,
     closeFormCompany, 
     sendingFormComany,
     updateFormCompany,
     sentFormComany, 
     clearErrorComp } = formCompanySlice.actions;

