import React from 'react'
import { useDispatch,useSelector } from 'react-redux';
import { crmApiComp,crmApiCont } from '../api/crmapi';
import { deleteCellCompany,
  deletingCellCompany,
    deleteCellContact,
  deletingCellContact,
  // showContact,
  showCompany,
  cambioLayout,
  // hideContact,
  // hideCompany
 } from '../store';
import Swal from 'sweetalert2'
export const useTabla = () => {
    const dispatch = useDispatch();
     const {isdeleteComp,showComp}=useSelector(state=>state.tableCompany)
     const {isdeleteCont,showCont}=useSelector(state=>state.tableContact)
   
    
    const deleteCompany=async(data)=>{
      dispatch(deletingCellCompany(true))
      // dispatch(hideContact())
      
      Swal.fire({
        title: 'Atencion!',
        text: 'Do you want to delete this line',
        icon: 'success',
        confirmButtonText: 'Cool'
      }).then((result) => {
        if (result.isConfirmed) {
          dispatch(deleteCellCompany(false))
          // console.log(isdeleteCont)
        }
        if(data){
          //  console.log(data);
           crmApiComp.delete(`/${data._id}`)
          return
       }
      })
    }
    const deleteContact=async(data)=>{
        dispatch(deletingCellContact(true))
        // dispatch(hideContact())
        
        Swal.fire({
          title: 'Atencion!',
          text: 'Do you want to delete this line',
          icon: 'success',
          confirmButtonText: 'Cool'
        }).then((result) => {
          if (result.isConfirmed) {
            dispatch(deleteCellContact(false))
            // console.log(isdeleteCont)
          }
          if(data){
            //  console.log(data);
             crmApiCont.delete(`/${data._id}`)
            return
         }
        })
        // dispatch(deleteCellContact())
        
    }
    const showInfoContact=()=>{
      dispatch(cambioLayout("info_contact"))
      // if(isdeleteCont) {
      //   return
      // }else{

      //   dispatch(cambioLayout("info_contact"))
      // }
      
    }
    const showInfoCompany=()=>{
      dispatch(cambioLayout("info_company"))
    }
  return {
    isdeleteComp,
    isdeleteCont,
    showComp,
    showCont,

    showInfoContact,
    deleteCompany,
    deleteContact,
    showInfoCompany
  }
}
