import { configureStore } from '@reduxjs/toolkit';
 import { formContactSlice,formCompanySlice} from './';
import { tableContactSlice,tableCompanySlice } from './';
import { infoCompanySlice } from './info/infoCompSlice';
import { infoContactSlice } from './info/infoContactSlice';
import {authSlice} from './auth/authSlice'
import { calendarSlice } from './calendar/calendarSlice';
import { uiSlice } from './ui/uiSlice';
import { layoutSlice } from './layout/layoutSlice';
import { noteSlice } from './notes/noteSlice.js';
import { emailSlice } from './emails/emailSlice';


export const store = configureStore({
    reducer: {
        triggerCont: formContactSlice.reducer,
        triggerComp: formCompanySlice.reducer,
        tableContact: tableContactSlice.reducer,
        tableCompany: tableCompanySlice.reducer,
        infoContact: infoContactSlice.reducer,
        infoCompany: infoCompanySlice.reducer,
        auth:     authSlice.reducer,
        calendar: calendarSlice.reducer,
        ui:       uiSlice.reducer,
        layout: layoutSlice.reducer,
        note: noteSlice.reducer,
        email: emailSlice.reducer,
    },
    middleware: (getDefaultMiddleware) => getDefaultMiddleware({
        serializableCheck: false
    })
})
