import React from 'react'
import { useAuthStore, useFormSetting, useLayout } from '../../hooks';

export const SetAddCompany = () => {
    const {user,startLogout}=useAuthStore();
    const {openFormComp}=useFormSetting();
    const {changeLayout}=useLayout();
    
   
    const logout=()=>{
        startLogout();
    }
    const addContact=()=>{
        openFormComp()
    }
  return (
    <>
    <div className="row mb-3">
                    <div className="col col-6">
                        <nav className="navbar navbar-expand-lg navbar-light bg-light">
                            <div className="container-fluid">

                                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                                    
                                    <div >
                                        <button type="button" onClick={()=>addContact()} className="btn btn-outline-primary ">Add Company</button>
                                    </div>

                                </div>
                            </div>
                        </nav>
                    </div>
                    <div className="col col-6 ">
                        <nav className="navbar navbar-expand-lg navbar-light bg-light me-0">
                            <div className="container-fluid">

                                <div className="collapse navbar-collapse justify-content-end" id="navbarNavAltMarkup">
                                   
                                    <div >
                                        <button type="button" onClick={logout} className="btn btn-outline-primary ">Logout</button>
                                    </div>

                                </div>
                                {/* Nombre de usuario conectado */}
                                
                            </div>
                        </nav>
                        <div>
                            <p className='float-end me-4'>{user.name}</p>
                        </div>
                    </div>
                </div>
</>
  )
}
