import React, { useState } from 'react'
import { useForm } from '../../hooks/useForm';
import { useFormSetting } from '../../hooks/useFormSetting';



export const FormContact = (infoContacts='') => {
    const {name_c,
        lastname_c,
        cargo_c,
        email_c,
        phone_c,
        country_c,
        company_c,
         onInputChange} = useForm(infoContacts.data);
    const {closeFormCont,updateFormContact ,sendingFormCont}=useFormSetting();
    const [first, setfirst] = useState(infoContacts)

    var date =new Date();
    var dd =String(date.getDate()).padStart(2,'0');
    var mm =String(date.getMonth()+1).padStart(2,'0');
    var yyyy = date.getFullYear();
    date = yyyy +'/'+mm +'/'+ dd ; 
    
    const close=()=>{
        closeFormCont();
        // e.preventDefault();
    
        }   
        
    const handleForm=(e)=>{
        e.preventDefault();
        
         sendingFormCont({...first.data,name_c,
            lastname_c,
            cargo_c,
            email_c,
            phone_c,
            country_c,
            company_c,
            fecha_creacion:date})
        // console.log(name_c);
        
    }
   
  return (
    <>
       <div className="rounded mt-3 form form__company form__company_container " id="contact_form">
                      <div className="mx-2 my-3 ">
                         {/* Formulario para las empresas  onSubmit={ handleLogin }*/}
                        <div className='form_cont'>
                            <form onSubmit={handleForm}>
                                <div className="form__company_first" id="comp1">
                                    <div className="form-group my-4">
                                        <input
                                            type="text"
                                            className="form__entradas form-control"
                                            placeholder="Company name"
                                            name="name_c"
                                            defaultValue={name_c}
                                            onChange={ onInputChange }
                                        />
                                    </div>
                                    <div className="form-group my-4">
                                        <input
                                            type="text"
                                            className="form__entradas form-control"
                                            placeholder="last name"
                                            name="lastname_c"
                                            defaultValue={lastname_c}
                                            onChange={ onInputChange }
                                        />
                                    </div>
                                    <div className="form-group my-4">
                                        <input
                                            type="text"
                                            className="form__entradas form-control"
                                            placeholder="Product Manager"
                                            name="cargo_c"
                                            defaultValue={cargo_c}
                                            onChange={ onInputChange }
                                        />
                                    </div>
                                    <div className="form-group my-4">
                                        <input
                                            type="text"
                                            className="form__entradas form-control"
                                            placeholder="algo@gmail.com"
                                            name="email_c"
                                            defaultValue={email_c}
                                            onChange={ onInputChange }
                                        />
                                    </div>
                                    <div className="form-group my-4">
                                        <input
                                            type="text"
                                            className="form__entradas form-control"
                                            placeholder="+49222288873"
                                            name="phone_c"
                                            defaultValue={phone_c}
                                            onChange={ onInputChange }
                                        />
                                    </div>
                                    <div className="form-group my-4">
                                        <input
                                            type="text"
                                            className="form__entradas form-control"
                                            placeholder="Country"
                                            name="country_c"
                                            defaultValue={country_c}
                                            onChange={ onInputChange }
                                        />
                                    </div>
                                    <div className="form-group my-4">
                                        <input
                                            type="text"
                                            className="form__entradas form-control"
                                            placeholder="Company"
                                            name="company_c"
                                            defaultValue={company_c}
                                            onChange={ onInputChange }
                                        />
                                    </div>
                                   
                            
                            
                                    <div className="form-group d-flex">
                                            <button type="submit"  id="ad_contact"
                                            className="btn btn_company bg-primary mt-3 form__entradas">Add</button>
                                       </div>
                                       <div className="form-group d-flex">
                                            <button type="submit"  id="close_contact"
                                            className="btn btn_company bg-primary mt-3 form__entradas"
                                             onClick={close}>Close</button>
                                       </div>
                                </div>
                            
                            
                            </form>
                        </div>
                    
                     </div>
 
 
                 </div>
    </>
  )     
}
