import React, { useEffect, useState} from "react";

import { FormCompany } from "../Formularios/FormCompany";
import { useFormSetting } from "../../hooks/useFormSetting";
import { useTabla } from "../../hooks/useTabla";
import { crmApiComp } from '../../api/crmapi';
import { useCard } from "../../hooks/useCard";
import { useLayout } from "../../hooks";


const initCompany={
    nombreEmpresa :'',
    calle :'',
    codigoPostal :'',
    ciudad :'',
    pais :'',
    idioma :'',
    homepage :'',
    moneda :'',
    tipoAgencia :'',
    codigoTourPlan :''
  
}
const imagen=require("../../image/trash.png");
const updates=require("../../image/update.png")
const Tabla_company=()=>{


    const { isOpenComp,openFormComp } = useFormSetting();
    const {isdeleteComp,deleteCompany,showInfoCompany}=useTabla();
    const [company,setCompany]=React.useState ([]);
    const [infoCompany, setInfoCompany] = useState(initCompany)
    const {showCardCompany,value,search,searchChange,searchDeactive}=useCard();
    const {showAll}=useLayout();
    // console.log(isOpen)
    useEffect(() => {
    loadData()
    }, [showAll,search])

    // useEffect(() => {
    // loadSearch()
    // }, [search])
    
    
    let cont=1;
    const loadData=async()=>{
            if(showAll){
                const resp = await crmApiComp.get( '/');
            setCompany(resp.data);
            }else{
                 const resp = await crmApiComp.get( `/${searchChange.type}/${searchChange.seleccion}`);
        setCompany(resp.data);
        searchDeactive();
            }
            
            // console.log("object");
        
    } 
    // const loadSearch=async()=>{
       
    // }

    const updateCell=(id)=>{
        openFormComp(id)
        setInfoCompany(id)
        // console.log(id)
    }
   
    const deleteCell=(id)=>{
        deleteCompany(id);
    //    alert(id._id);
    }
    const show=(item)=>{
        showInfoCompany();//Para decir que se va a mostrar Card de contacts
        showCardCompany(item)
    //    console.log(item);
   }
     
    
        return (

        <div className="lateralDerecho tables__position" id="lateralDerecho"> 
            
            {/* Formulario para las empresas */}
            {
            
            isOpenComp&& 
                <div className="rounded mt-3 form form__company form__company_container " id="company_form">
                    <div className="mx-2 my-3 ">
                        <FormCompany data={infoCompany} /> 
                    </div>
                </div>
            }
                    
                
                
            <div className="container tables__scroll">
                        {
                        !isOpenComp&& <table className="tables__company" id="tabla_company">
                <thead className="thead-dark">
                    <tr>
                    <th scope="col" className="tables__first_th">#</th>
                    <th scope="col" >Company name</th>
                    <th scope="col">Address</th>
                    <th scope="col">Post code</th>
                    <th scope="col">Country</th>
                    <th scope="col">City</th>
                    <th scope="col">Language</th>
                    <th scope="col">Homepage</th>
                    <th scope="col">Currency</th>
                    <th scope="col">Kind of Agency</th>
                    <th scope="col">Tourplan code</th>
                    <th scope="col">Potencial Agency</th>
                    <th scope="col">Domain</th>
                
                    </tr>
                </thead>
                <tbody>
                    {
                        
                        company.map((item)=>{
                            return(
                                <tr key={item._id} onDoubleClick={()=>show(item)}>
                            <td scope="row" >
                                            <button className="tables__button"><img  src={updates} className="tables__icon" onClick={()=>updateCell(item)}/></button>
                                            <button className="tables__button"><img  src={imagen} className="tables__icon" onClick={()=>deleteCell(item)}/></button>{cont++}
                                        </td>
                            <td  >{item.nombreEmpresa}</td>
                            <td >{item.calle}</td>
                            <td>{item.codigoPostal}</td>
                            <td>{item.ciudad}</td>
                            <td>{item.pais}</td>
                            <td>{item.idioma}</td>
                            <td>{item.homepage}</td>
                            <td>{item.moneda}</td>
                            <td>{item.tipoAgencia}</td>
                            <td>{item.codigoTourPlan}</td>
                            <td>{item.codigoTourPlan}</td>
                            <td>{item.domain}</td>
                            </tr>

                            )
                            
                        })
                    }
                    
                    
                </tbody>
            </table>
                        }
            
            </div>

            </div>
    )
 
 
 }
export default Tabla_company;


