import axios from "axios";
import React, { useEffect, useState } from "react";
import { crmApiCont } from '../../api/crmapi';
import { useLayout } from "../../hooks";
import { useCard } from "../../hooks/useCard";
import { useFormSetting } from "../../hooks/useFormSetting";
import { useTabla } from "../../hooks/useTabla";
import { FormContact } from "../Formularios/FormContact";

import {ModalForm} from "../modal"

const updates=require("../../image/update.png")
const imagen=require("../../image/trash.png")
const initContact={
    name_c:'',
    lastname_c:'',
    email_c:'',
    phone_c:'',
    country_c:'',
    contact_c:'',
    fecha_creacion:''

}
const Tabla_contactos=()=>{

    const {values}=useCard();
    const [contact,setContact]=React.useState ([]);
    const [infoContact, setInfoContact] = useState(values)
    const { isOpen,openFormCont } = useFormSetting();
    const {deleteContact,showInfoContact,isdeleteCont}=useTabla();
    const {showCardContact,value,search,searchChange,searchDeactive}=useCard();
    const {showAll}=useLayout();
    
    let cont=1;
    useEffect(() => {
        loadData()
        }, [showAll,search])
    
        useEffect(() => {
        loadSearch()
        }, [search])
        
        
     
        const loadData=async()=>{
                if(showAll){
                    const resp = await crmApiCont.get( '/');
                setContact(resp.data);
                }
                
                // console.log("object");
            
        } 
        const loadSearch=async()=>{
            const resp = await crmApiCont.get( `/${searchChange.type}/${searchChange.seleccion}`);
            setContact(resp.data);
            searchDeactive();
        }

    const updateCell=(id)=>{
        openFormCont(id)
        setInfoContact(id)
        // console.log(id)
    }
    const show=(item)=>{
          showInfoContact();//Para decir que se va a mostrar Card de contacts
          showCardContact(item)//Para enviar la informacion del
        // console.log(item);
    }
    const deleteCell=(id)=>{
        deleteContact(id);
    //    alert(id._id);
    }
        return (
            <div className="tables__position">
                {
                    isOpen&&<div className="mx-2 my-3 ">   
                        <FormContact data={infoContact} />
                        </div>
                }
                
                {
                    !isOpen&&
                    <table className="table">
                    <thead className="thead-dark">
                        <tr>
                        <th scope="col" className="tables__first_th">#</th>
                        <th scope="col" >Name</th>
                        <th scope="col" >Last Name</th>
                        <th scope="col" >Email</th>
                        <th scope="col" >Phone</th>
                        <th scope="col" >Country</th>
                        <th scope="col" >Company</th>
                        <th scope="col" >Cargo</th>
                       
                        </tr>
                    </thead>
                    <tbody>
                        {
                                // onClick={()=>show(item)}
                            contact.map((item)=>{
                                return(
                                   
                                    <tr key={item._id} onDoubleClick={()=>show(item)}>
                                        <td scope="row" >
                                            <button className="tables__button"><img  src={updates} className="tables__icon" onClick={()=>updateCell(item)}/></button>
                                            <button className="tables__button"><img  src={imagen} className="tables__icon" onClick={()=>deleteCell(item)}/></button>{cont++}
                                        </td>
                                    <td >{item.name_c}</td>
                                    <td>{item.lastname_c}</td>
                                    <td>{item.email_c}</td>
                                    <td>{item.phone_c}</td>
                                    <td>{item.country_c}</td>
                                    <td>{item.company_c}</td>
                                    <td>{item.cargo_c}</td>
                                
                                    </tr>
    
                                )
                                
                            })
                        }
                    </tbody>
                    </table> 

                    // <div>
                    //     <div className="container text-start ">
                            
                    //         <div className="row row-cols-7 border border-2">
                                
                    //             <div className="col">Setting</div>
                    //             <div className="col">Name</div>
                    //             <div className="col">Last Name</div>
                    //             <div className="col">Email</div>
                    //             <div className="col">Phone</div>
                    //             <div className="col">Country</div>
                    //             <div className="col">Contact</div>
                        
                    //         </div>
                    //     </div>
                    //         <div className="container text-start ">
                    //         {
                    //             contact.map((item)=>{
                    //                         return(
                    //                             <div className=" border border-bottom-2 d-flex">
                    //                                 <div className="me-3 ">
                    //                                     <button className="tables__button"><img  src={updates} className="tables__icon" onClick={()=>dobleClick(item)} key={item._id}/></button>
                    //                                     <button className="tables__button"><img  src={imagen} className="tables__icon" onClick={()=>deleteCell(item)} key={item._id}/></button>
                    //                                 </div>
                    //                                     <div className="row row-cols-7 border border-bottom-2" onClick={()=>show(item)} key={item._id}>
                    //                                         <div className="col  ">{item.name_c}</div>
                    //                                         <div className="col">{item.lastname_c}</div>
                    //                                         <div className="col">{item.email_c}</div>
                    //                                         <div className="col">{item.phone_c}</div>
                    //                                         <div className="col">{item.country_c}</div>
                    //                                         <div className="col">{item.contact_c}</div>
                    //                                     </div>
                                                        
                    //                              </div> 
                    //                          )
                                            
                    //                     })
                    //         }
                                           
                    //     </div>
                    // </div>


                }   

            </div>
        )

    
}
export default Tabla_contactos;