import React from 'react'
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux'
import { activeSearch,deactiveSearch, cardContact, cardContactClean, changeSearch, mostrarTodo } from '../store';
import { cardCompany } from '../store/info/infoCompSlice';

export const useCard = () => {
const dispatch=useDispatch();
const { name_c,lastname_c,cargo_c,email_c,phone_c,country_c,company_c}  = useSelector( state => state.infoContact );
const { nombreEmpresa,
calle,
codigoPostal,
ciudad,
pais,
idioma,
homepage,
moneda,
tipoAgencia,
codigoTourPlan,domain}  = useSelector( state => state.infoCompany );
const values={ name_c,lastname_c,cargo_c,email_c,phone_c,country_c,company_c}
const valuesComp={nombreEmpresa,
    calle,
    codigoPostal,
    ciudad,
    pais,
    idioma,
    homepage,
    moneda,
    tipoAgencia,
    codigoTourPlan,domain}
const {searchChange,search}=useSelector(state=>state.tableContact)

const showCardContact=(data)=>{
    dispatch(cardContact(data))
    }
const hideCardContact=(data)=>{
    dispatch(cardContact(data))
    }

    const cleanFormCont=()=>{
        dispatch(cardContactClean())
      }

const showCardCompany=(data)=>{
    // console.log(data)
    dispatch(cardCompany(data))
    }
const searchActive=(data)=>{
    // console.log(data);
    dispatch(activeSearch(data))
} 
const searchAnilize=(data)=>{
    // console.log(data)
    dispatch(changeSearch(data))
}   
const searchDeactive=()=>{
    dispatch(mostrarTodo(false))
}

 return {
    values,
    valuesComp,
    search,
    searchChange,
    cleanFormCont,
    hideCardContact,
    showCardContact,
    showCardCompany,
    searchActive,
    searchDeactive,
    searchAnilize
}
}