import React, { Fragment} from "react";
import { useCard } from "../../../hooks/useCard";
import { useTabla } from "../../../hooks/useTabla";
import { ContactCard } from "./contactCard";
import { SearchContact } from "./SearchContact";

// }
export const Contacts=(info={})=>{

    const {showCont}=useTabla();
    const {isdeleteCont}=useCard();
    const {name_c,lastname_c,cargo_c,email_c,phone_c,country_c,company_c}=useCard();
    const values={name_c,lastname_c,cargo_c,email_c,phone_c,country_c,company_c}
    
    return(
        <div >
            {
                showCont?<ContactCard value={values}/>:<SearchContact /> 
                
            }
        </div>
    )
}