import { createSlice } from '@reduxjs/toolkit';
import { stat } from 'fs/promises';

export const formContactSlice = createSlice({
    name: 'triggerCont',
   
    initialState: {
        isOpen: false,
        isClose:true, 
        tablaShow:true,       
        errorMessage: undefined,
    },
    reducers: {
     
        openFormContact: ( state ) => {
            state.isOpen = true;      
            state.isClose=false;     
            state.tablaShow=false; 
            state.errorMessage = undefined;
        },
        closeFormContact: ( state, { payload } ) => {
            state.isOpen = false;   
            state.isClose=true;           
            state.tablaShow=true; 
            state.errorMessage = undefined;
        },
       
        clearErrorCont: ( state ) => {
            state.errorMessage = undefined;
        },
        sendingFormCont: ( state ) => {
            state.errorMessage = undefined;
        },
        sentFormCont: ( state ) => {
            state.errorMessage = undefined;
        }
    }
});


// Action creators are generated for each case reducer function
export const { 
    openFormContact, 
    closeFormContact,
     sendingFormCont,
     sentFormCont,
      clearErrorCont,     
     } = formContactSlice.actions;

