import { createSlice } from '@reduxjs/toolkit';

export const layoutSlice = createSlice({
    name: 'layout',
    initialState: {
        tipo:'contacto',
        showAll:true
    },
    reducers: {
        cambioLayout: (state, {payload} ) => {
         state.tipo = payload;
        },
        mostrarTodo:(state, {payload} )=>{
            state.showAll=payload
        }
    }
});


// Action creators are generated for each case reducer function
export const { cambioLayout,mostrarTodo } = layoutSlice.actions;