import React from 'react'
import { useDispatch } from 'react-redux'
import { useAuthStore } from '../../hooks';
import { useFormSetting } from '../../hooks/useFormSetting'

export const SetCompany = () => {

  const {user,startLogout}=useAuthStore();
  const {openFormComp}=useFormSetting();
  
  const logout=()=>{
    startLogout();
}
  return (
    <div>
       <nav className="navbar navbar-expand-lg navbar-light bg-light me-0">
                        <div className="container-fluid">
                            <div className="collapse navbar-collapse justify-content-end" id="navbarNavAltMarkup">
                
                                <div >
                                    <button type="button" onClick={logout} className="btn btn-outline-primary ">Logout</button>
                                </div>
                            </div>
                            {/* Nombre de usuario conectado */}
                
                        </div>
                    </nav>
                    <div>
                        <p className='float-end me-4'>{user.name}</p>
                    </div>
    </div>
  )
}
