import Navigator from "../../components/navigator";
import LateralDerecho from '../../components/Layout/lateralDerecho'
import LateralIzquierdo from '../../components/Layout/lateralIzquierdo'
import { useLayout } from "../../hooks";

export const LayoutScreen=()=>{
   const {tipo}=useLayout();
        return (
            <div className="todo" id="todo">
               
                 <div><Navigator/></div>
               <div className="custom-container text-start my-auto cuerpo" id="cuerpo">

                    <div className="row my-3 " id="mi_posicion">
                        <div className="col col-3  " id="iz">
                            <LateralIzquierdo information={tipo}/>

                        </div>
                         <div className="col col-9 pl-0" id="dr">
                          <div className="bg-light h-100 rounded shadow ">
                             <LateralDerecho information={tipo}/>

                            </div>

                        </div> 

                    </div>

                </div>


                <div className=" h-25 bg-primary">
                    <p>Copy Right</p>
                </div>
            </div>
        );
}