import { createSlice } from '@reduxjs/toolkit';


export const tableCompanySlice = createSlice({
    name: 'tableCompany',
    initialState: {
        isdeleteComp: false,
        showComp:false
    },
    reducers: {
        updateComp: (state, /* action */ ) => {
         state.updateComp=true;
        },
        deletingCellCompany:(state)=>{
            state.isdeleteComp=true
        },
        deleteCellCompany:(state)=>{
            state.isdeleteComp=false
        },
        showCompany:(state)=>{
            state.showComp=true
        },
        hideCompany:(state)=>{
            state.showComp=false
        }
    }
});


// Action creators are generated for each case reducer function
export const { deleteCellCompany,deletingCellCompany,showCompany,hideCompany } = tableCompanySlice.actions;