import React, { useState } from 'react'
import { useDispatch } from 'react-redux';
import { useForm } from '../../hooks/useForm';
import { useFormSetting } from '../../hooks/useFormSetting';

const initCompany={
    nombreEmpresa :'',
    calle :'',
    codigoPostal :'',
    ciudad :'',
    pais :'',
    idioma :'',
    homepage :'',
    moneda :'',
    tipoAgencia :'',
    codigoTourPlan :'',
    domain:''
  
}
export const FormCompany = (infoCompany='') => {

    const { nombreEmpresa,
        calle ,
        codigoPostal ,
        ciudad ,
        pais ,
        idioma ,
        homepage ,
        moneda ,
        tipoAgencia ,
        codigoTourPlan ,
        domain, onInputChange} = useForm(infoCompany.data);
    const [first, setfirst] = useState(infoCompany)
    const {closeFormComp,sendingFormComp}=useFormSetting();
    const dispatch = useDispatch();
    // const {
    //     nombreEmpresa,
    //         calle ,
    //         codigoPostal ,
    //         ciudad ,
    //         pais ,
    //         idioma ,
    //         homepage ,
    //         moneda ,
    //         tipoAgencia ,
    //         codigoTourPlan ,
        //     }=infoCompany.data.nombreEmpresa===''?values:values.data
        var date =new Date();
        var dd =String(date.getDate()).padStart(2,'0');
        var mm =String(date.getMonth()+1).padStart(2,'0');
        var yyyy = date.getFullYear();
        date = yyyy +'/'+mm +'/'+ dd ; 
        
    const handleForm=(e)=>{
        e.preventDefault();
        // dispatch(triggerCompany(values))
        sendingFormComp({...first.data, nombreEmpresa,
            calle ,
            codigoPostal ,
            ciudad ,
            pais ,
            idioma ,
            homepage ,
            moneda ,
            tipoAgencia ,
            codigoTourPlan ,
            fecha_entrada:date,
            domain
        })
        
    }
    const next=()=>{
        const compForm1=document.getElementById("comp1");
        const compForm2=document.getElementById("comp2");
        compForm1.style.display="none";
        compForm2.style.display="block";
    }
    const previous=()=>{
        const compForm1=document.getElementById("comp1");
        const compForm2=document.getElementById("comp2");
        compForm1.style.display="block";
         compForm2.style.display="none";
    }
    const close=(e)=>{
    
    e.preventDefault();
    closeFormComp();

    }   
  return (
    <>
      
            <form onSubmit={ handleForm }>
                <div className="form__company_first" id="comp1">
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Company name"
                            name="nombreEmpresa"
                            defaultValue={nombreEmpresa}
                            // value={nombreEmpresa}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Address"
                            name="calle"
                            // value={calle}
                            defaultValue={calle}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Post code"
                            name="codigoPostal"
                            value={codigoPostal}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="city"
                            name="ciudad"
                            value={ciudad}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Country"
                            name="pais"
                            value={pais}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Language"
                            name="idioma"
                            value={idioma}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Homepage"
                            name="homepage"
                            value={homepage}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Currency"
                            name="moneda"
                            value={moneda}
                            onChange={ onInputChange }
                        />
                    </div>
            
                    <div className="form-group my-4">
                            <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Kind of Agency"
                            name="tipoAgencia"
                            value={tipoAgencia}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="Tour Plan code"
                            name="codigoTourPlan"
                            value={codigoTourPlan}
                            onChange={ onInputChange }
                        />
                    </div>
                    <div className="form-group my-4">
                        <input
                            type="text"
                            className="form__entradas form-control"
                            placeholder="@empresa.com"
                            name="domain"
                            value={domain}
                            onChange={ onInputChange }
                        />
                    </div>
            
                    <div className="form-group d-flex">
                        <button type="submit"  id="ad_contact"
                        className="btn btn_company bg-primary mt-3 form__entradas">Add</button>
                    </div>
                    <div className="form-group d-flex">
                        <button type="submit"  id="close_contact"
                        className="btn btn_company bg-primary mt-3 form__entradas"
                            onClick={close}>Close</button>
                    </div>
                    {/* <button onClick={()=>previous()} className="form__button_previous">previous</button> */}
                    {/* <input onClick={()=>next()} className="form__button_next" type="button" value="next"/> */}
                </div>
                <div className="form__company_second container" id="comp2">
                
                </div>
            
            </form>
        
    </>
  )
}
