import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { openFormCompany,
         closeFormCompany,
         openFormContact,
         closeFormContact, 
         sentFormComany, 
         sentFormCont,
                
         } from '../store';
import { crmApiComp,crmApiCont } from '../api/crmapi';


export const useFormSetting = () => {
    const dispatch = useDispatch();
    const { isOpenComp} = useSelector( state => state.triggerComp );
    const { isOpen} = useSelector( state => state.triggerCont );

    const openFormComp=(valor='')=>{
      // console.log(valor)
      if(valor!==''){
         // dispatch(updateFormCompany())
         dispatch(openFormCompany())
         return
      }
       dispatch(openFormCompany())
    }

    


    const closeFormComp=()=>{
       dispatch(closeFormCompany())
    }
    const sendingFormComp=async(data)=>{
      // Envio de los datos
      console.log(data);
      if(data._id){
        
         await crmApiComp.put(`/${data._id}`,data)
         return
      }
      await crmApiComp.post('/',data)


      try {
         dispatch(sentFormComany())
      } catch (error) {
         // dispatch()
      }

   }
    const openFormCont=(valor='')=>{
      // console.log(valor)

      if(valor!==''){
         // console.log("valor")
         // dispatch(updateFormContact())
         dispatch(openFormContact())
         return
      }
       dispatch(openFormContact())
    }

    const closeFormCont=()=>{
       dispatch(closeFormContact())
       
    }
    
    const sendingFormCont=async(data)=>{
      // dispatch(sendingFormCont())
      // Envio de los datos
        console.log(data)
      // if(data._id){
        
      //    await crmApiCont.put(`/${data._id}`,data)
      //    return
      // }
      // await crmApiCont.post('/',data)
     

      // try {
      //    dispatch(sentFormComany())
      // } catch (error) {
        
      // }
      // try {
      //    dispatch(sentFormCont())
      // } catch (error) {
        
      // }
    }
  return {    
   //Propieties
       isOpenComp,
       isOpen,
       

       //Method
      
      openFormComp,
      closeFormComp,
      openFormCont,
      closeFormCont,
      sendingFormCont,
      sendingFormComp
  }
  
}
