import axios from 'axios';


export const crmApiCont = axios.create({
    baseURL: 'http://localhost:3001/api/contact'
});
export const crmApiComp = axios.create({
    baseURL: 'http://localhost:3001/api/company'
});
export const crmApiAuth = axios.create({
    baseURL: 'http://localhost:3001/api/auth'
});
 const crmApiCalendar = axios.create({
    baseURL: 'http://localhost:3001/api/events'
});
export const crmApiNotes = axios.create({
    baseURL: 'http://localhost:3001/api/notes'
});
export const crmApiEmails = axios.create({
    baseURL: 'http://localhost:3001/api/emails'
});

crmApiCalendar.interceptors.request.use( config => {

    config.headers = {
        ...config.headers,
        'x-token': localStorage.getItem('token')
    }

    return config;
})


export default crmApiCalendar;
