import React from 'react'

export const CompanyCard = ({value=''}) => {
 
    const imagen=require('../../../image/avatar1.jpg')
  return (
    // left left__select px-4
    <div className="border-top left">
        {/* Foto del contacto */}
        <div className='left__head'>
            <div className='left__marco'>
                <img src={imagen} className='left__image'/>
            </div>
        </div>
        {/* Nombre de la empresa*/}
        <div className='left__name'>
            <span className='d-flex'><h3 className='left__name_main'>{value.name_c}</h3><h3>{value.nombreEmpresa}</h3></span>
        </div>      
        {/* Titulo del contacto */}
        
        {/* Informacion del contacto */}
        <div>
          <label htmlFor="">Address</label>
          <div className='left__line'></div>
          <span className='left__info'><p >{value.calle}</p></span>
          
            <label htmlFor="">Postal Code</label>
            <div className='left__line'></div>
          <span className='left__info'><p>{value.codigoPostal}</p></span> 

          <label htmlFor="">Country</label>
          <div className='left__line'></div> 
          <span className='left__info'><p>{value.pais}</p></span>  

          <label htmlFor="">Language</label>
          <div className='left__line'></div>
          <span className='left__info'><p>{value.idioma}</p></span> 
          
          <label htmlFor="">Web site</label>
          <div className='left__line'></div>
          <span className='left__info'><p>{value.homepage}</p></span>  

          <label htmlFor="">Currency</label>
          <div className='left__line'></div>
          <span className='left__info'><p>{value.moneda}</p></span>  

          <label htmlFor="">Kind of Agency</label>
          <div className='left__line'></div>
          <span className='left__info'><p>{value.tipoAgencia}</p></span>  

          <label htmlFor="">Tour Plan code</label>
          <div className='left__line'></div>
          <span className='left__info'><p>{value.codigoTourPlan}</p></span>  
        </div>
       
    </div>
  )
}
