import { createSlice } from '@reduxjs/toolkit';

export const infoCompanySlice = createSlice({
    name: 'infoCompany',
    initialState: {
        nombreEmpresa:'',
        calle:'',
        codigoPostal:'',
        ciudad:'',
        pais:'',
        idioma:'',
        homepage:'',
        moneda:'',
        tipoAgencia:'',
        codigoTourPlan:'',
        domain:''
      
    },
    reducers: {
        cardCompany: (state, {payload} ) => {
         state.nombreEmpresa=payload.nombreEmpresa;
         state.calle=payload.calle;
         state.codigoPostal=payload.codigoPostal;
         state.ciudad=payload.ciudad;
         state.pais=payload.pais;
         state.idioma=payload.idioma;
         state.homepage=payload.homepage;
         state.moneda=payload.moneda;
         state.tipoAgencia=payload.tipoAgencia;
         state.codigoTourPlan=payload.codigoTourPlan;
         state.domain=payload.domain;
        }
    }
});


// Action creators are generated for each case reducer function
export const { cardCompany } = infoCompanySlice.actions;