import React,{useEffect,useState} from 'react'
import { crmApiNotes,crmApiEmails } from '../../api/crmapi'
import { useCard, useTools } from '../../hooks';

export const InfoCompany = ({value=''}) => {

  const [notes, setNotes] = useState([])
  const [emails, setEmails] = useState([])
  // const {values}=useCard();
  const {noteOpen,emailOpen}=useTools();

  useEffect(() => {
    loadData()
    loadDataEmail()
    }, [])
  
    
    
  const loadData=async()=>{
    const resp = await crmApiNotes.get( `/domain/${value.domain}`,value);
        setNotes(resp.data);
  }  
  const loadDataEmail=async()=>{
    const resp = await crmApiEmails.get( `/domain/${value.domain}`,value);
        setEmails(resp.data);
  }  
  return (
    <div className='right__info' >
    {
      !noteOpen&&notes.map((note)=>{
        return(
          <div className="card mx-3 mb-3 right__card" key={note._id}>
            <div className='d-flex justify-content-between right__header '>
              <div className='mx-3'>{note.email_c}</div>
              <div className='mx-3'>{note.date}</div>
            </div>
            <div className="card-body">{note.note}</div>              
          </div>
        )
      })
    }
    {
      !emailOpen&&emails.map((email)=>{
        return(
          <div className="card mx-3 mb-3 right__card" key={email._id} >
            <div className='d-flex justify-content-between right__header right__header-email '>
              <div className='mx-3'><strong>From:</strong> {email.email}</div>
              <div className='mx-3'><strong>To:</strong> {email.email_c}</div>
              <div className='mx-3'>{email.date}</div>
            </div>
            <div className="card-body">{email.data}</div>              
          </div>
        )
      })
    }
  </div>
  )
}
