import { Link, NavLink, useHistory } from 'react-router-dom';
import { useLayout } from '../hooks';
const logo =require('../image/logoazul.png')

const Navigator =()=>{

    const {changeLayout,muestraTodo}=useLayout();

    const changesLayout=(data)=>{
        changeLayout(data);
        muestraTodo(true);
    }
    return(
        <nav className="navbar navbar-expand-lg navbar-dark bg-primary shadow">
            <div className="header__container container-fluid ">
                <div className='header__logo'>
                    <a className="navbar-brand" href="#">
                        <img  src={logo} className="header__imagen"/> 
                    </a>
                </div>
                
                <div className="collapse navbar-collapse header" id="navbarSupportedContent">
                   
                    
                    <div className="navbar-collapse">
                        <div className="navbar-nav">
                           
                            <button onClick={()=>{changesLayout("contacto")}} className="border-0 bg-transparent text-white" >Contacts</button>
                            {/* <NavLink 
                                activeClassName="active"
                                className="nav-item nav-link" 
                                exact
                                to="/company"
                            >
                                Companies
                            </NavLink> */}
                            <button onClick={()=>{changesLayout("company")}} className="border-0 bg-transparent text-white">Companies</button>
                    
                        </div>
                    </div>


                </div>
            </div>
        </nav>

    )
}

export default Navigator;