import { useCard } from "../../hooks";
import "../../stylesheet/main.scss"
import { CompanyCard } from "./Izquierda/companyCard";
import { Company } from "./Izquierda/companySettings";
import { ContactCard } from "./Izquierda/contactCard";
import {Contacts} from "./Izquierda/contactSettings";
import { SearchContact } from "./Izquierda/SearchContact";
// import {muestraTabla,muestraForm,muestraData} from "../../redux/actions";
// import {connect} from "react-redux";
// import Ficha from "./Izquierda/ficha";
// import Empresa from "./Izquierda/empresa";
// import Contactos from "./Izquierda/contactos";


// import React from 'react'
// type AddButtom={
//     type_buton:string;
// }

// type Information={
//     information:string;
//   }
 const LateralIzquierdo = ({information=''}) => {

  const {values,valuesComp}=useCard();
    
     const selection=()=>{
        switch (information) {
         case "contacto":
           return <SearchContact />
         case "company":
           return  <Company />
         case "calendar":
           return  <Contacts />
         case "info_contact":
           return  <ContactCard value={values}/>
         case "info_company":
           return  <CompanyCard value={valuesComp}/>
         default:
           break;
       }
      
      } 
  return (
  
     <div className="col bg-light h-100 rounded-3 shadow">

     <section className=" mx-auto ">
       
        {/* <div >
            <button type="submit" onClick={addContact}
                    className="btn bt btn-outline-primary btn-block mb-1 adicionar " id="add_cont" >
                        {type_buton}
            </button>
           
        </div> */}
        {
            selection()
        }
        </section>
        
    </div>
  )
}
export default LateralIzquierdo



